<template>
  <div class="banner">
    <img
      class="img"
      v-for="(item,index) in images"
      :key="item.id"
      v-show="index===currentIndex"
      :src="item.src"
    >
  </div>
</template>
<script>
export default {
  data() {
    return {
      images: [
        { id: 0, src: require("../assets/img/1109234.jpg") },
        { id: 1, src: require("../assets/img/1106740.jpg") }
      ],
      currentIndex: 0
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.timer = setInterval(() => {
        if (this.currentIndex >= this.images.length - 1) {
          this.currentIndex = 0;
        } else {
          this.currentIndex++;
        }
      }, 2000);
    });
  }
};
</script>
<style lang="scss" scoped>
.banner {
  width: 100%;
  // height: auto;
  .img {
    width: 100%;
  }
}
</style>



